<template>
    <div class="mr-5 ml-5 mb-5">
        <div class="text-left mb-2">
            <router-link class="btn btn-light" to="/">Kembali</router-link>
        </div>
        <div class="row">
            <div class="col-md-8">
                <content-loader v-if="selectedPortfolio.thumbnail == ''" height="300" :speed="2" :animation="true"></content-loader>
                <div class="tab-content" v-else>
                    <div class="tab-pane fade" :class="[{'show active': $route.hash === `${tn.id}` || $route.hash === '' && tn.id == '#thumbnail-1'}]" :id="tn.id" role="tabpanel" v-for="tn in selectedPortfolio.thumbnail" :key="tn.id">
                        <img :src="require(`../assets/portfolio/${tn.image}`)" class="img-fluid portfolio-thumbnail img-responsive">
                    </div>
                </div>
            </div>
            <div class="col-md-4 text-left">
                <div class="card shadow">
                    <div class="card-body">
                        <h4>Thumbnail</h4>
                        <div v-if="selectedPortfolio.thumbnail == ''" >
                            <content-loader :speed="2" :animate="true" height="65"></content-loader>
                            <div class="mt-2"/>
                            <content-loader :speed="2" :animate="true" height="65"></content-loader>
                            <div class="mt-2"/>
                            <content-loader :speed="2" :animate="true" height="65"></content-loader>
                            <div class="mt-2"/>
                            <content-loader :speed="2" :animate="true" height="65"></content-loader>
                        </div>
                        <div class="nav flex-column nav-pills" v-else role="tablist" aria-orientation="vertical">
                            <a v-for="tn in selectedPortfolio.thumbnail" :key="tn.id" :href="tn.id" class="nav-link mt-2" :class="[{'show active': $route.hash === tn.id || $route.hash === '' && tn.id == '#thumbnail-1'}]" data-toggle="pill" role="tab">{{ tn.name }}</a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="information text-left mt-5 pt-3">
            <div class="row justify">
                <div class="col-md-8">
                    <content-loader v-if="selectedPortfolio.name == ''" :speed="2" :animate="true" height="42" style="margin-top: 50px !important;"></content-loader>
                    <div v-else><h2 class="app-title">{{ selectedPortfolio.name }}</h2><p class="badge badge-primary">{{ selectedPortfolio.role }}</p></div>
                    <div v-if="selectedPortfolio.description == ''">
                        <div class="mt-2">
                            <content-loader :speed="2" :animate="true" height="150"></content-loader>
                        </div>
                    </div>
                    <p class="app-description" v-html="selectedPortfolio.description"><br><br></p>
                </div>
                <div class="col-md-4">
                    <h2 class="tools-title">Tools</h2>
                    <p class="tools-description">Tools yang digunakan selama pembuatan</p>
                    <div v-if="selectedPortfolio.tools == ''" >
                        <content-loader :speed="2" :animate="true" height="65"></content-loader>
                        <div class="mt-2"/>
                        <content-loader :speed="2" :animate="true" height="65"></content-loader>
                    </div>
                    <div v-else v-for="tool in selectedPortfolio.tools" v-bind:key="tool.name">
                        <div class="item-tool mt-3">
                            <img :src="require(`../assets/tools/${tool.icon}`)" alt="" class="item-tool-img">
                            <div class="description">
                                <p>{{ tool.name }}</p>
                                <a :href="tool.link_download" class="link text-primary">Download</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<style>
    @font-face {
        font-family: 'Maison-Regular';
        src: url('../assets/fonts/MaisonNeueExtended-Book.otf');
    }
    @font-face {
        font-family: 'Maison-Light';
        src: url('../assets/fonts/MaisonNeueExtended-Light.otf');
    }
    @font-face {
        font-family: 'Maison-Medium';
        src: url('../assets/fonts/MaisonNeueExtended-Medium.otf');
    }
    @font-face {
        font-family: 'Maison-Bold';
        src: url('../assets/fonts/MaisonNeueExtended-Bold.otf');
    }
    .portfolio-thumbnail {
        border-radius: 15px;
        height: 500px !important;
    }

    .app-title, .tools-title {
        font-family: 'Maison-Medium';
    }
    
    .app-description, .tools-description {
        font-family: 'Maison-Light';
    }

    .item-tool {
        overflow: hidden;
        margin-bottom: 10px;
    }

    .item-tool .link {
        font-size: 14px;
        margin-top: -8px;
    }

    .item-tool img {
        width: 50px;
        height: 50px;
        float: left;
    }

    .item-tool .description {
        float: left;
        overflow: hidden;
        margin-left: 12px;
    }

    .item-tool p {
        margin: 0;
        font-family: 'Maison-Medium'
    }

    .nav-pills {
        font-family: 'Maison-Light';
    }

    .nav-pills .nav-link {
        background-color: #fafafa;
        color: #312e51;
    }

    .tab-pane.active {
        animation: slide-down 2s forwards;
    }

    @keyframes slide-down {
        0% { opacity: 0; transform: translateX(0%); }
        100% { opacity: 1; transform: translateX(100); }
    }

    @keyframes zoominoutsinglefeatured {
        0% {
            transform: scale(0.3,0.3);
        }
        50% {
            transform: scale(0.5,0.5.0.5);
        }
        100% {
            transform: scale(1,1);
        }
    }

    .nav-link {
        transition: all 0.9s ease-out
    }
    
</style>

<script>
import { ContentLoader } from 'vue-content-loader'
export default {
    name: "PortfolioDetail",
    components: {
        ContentLoader
    },
    data: function () {
        return {
            allPortfolio: [
            ],
            selectedPortfolio: {
                id: '',
                name: '',
                thumbnail: '',
                description: '',
                tools: ''
            }
        }
    },
    created: function () {
        const allPortfolio = require('../portfolio.json')
        allPortfolio.map(data => {
            if(data.name == this.$route.params.name) {
                setTimeout(() => {
                    this.selectedPortfolio = data;
                }, 1500);
            }
        })
    }
}
</script>